#preferences {
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  height: calc(100vh - 3px);
  display: flex;
  flex-direction: column;
  color: rgb(0, 0, 0);
  background-color: rgb(230, 230, 230);
}

#preferences header {
  padding: 24px 48px 12px 48px;
}
#preferences header div:nth-child(1) {
  font-size: 28px;
  text-transform: capitalize;
}
#preferences header div:nth-child(2) {
  font-size: 14px;
  padding-left: 1px;
  opacity: 0.8;
}

#preferences main {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#preferences main .centered {
  display: flex;
  flex-direction: row;
  border-radius: 5px;
  max-height: 100%;
  overflow-y: hidden;
}
#preferences main .centered .left {
  padding: 32px 48px 32px 48px;
  min-width: 320px;
  max-width: 50%;
  overflow-y: scroll;
  border-right: 1px solid rgba(0, 0, 0, 0.07);
}
#preferences main .centered .right {
  padding: 32px 48px 32px 48px;
  min-width: 320px;
  max-width: 50%;
  overflow-y: scroll;
}

#preferences footer {
  text-align: right;
  padding: 16px 48px 24px 48px;
}
#preferences footer.disabled {
  opacity: 0.3;
}
#preferences footer button span {
  font-size: 11px;
  text-transform: uppercase;
}

#preferences .label {
  padding-bottom: 12px;
}
#preferences .label div:nth-child(1) {
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 600;
}
#preferences .label div:nth-child(2) {
  padding-top: 6px;
  padding-bottom: 6px;
  font-size: 11px;
  opacity: 0.8;
}
#preferences .options:not(:last-child) {
  margin-bottom: 32px;
}
#preferences .folder:not(:last-child) {
  margin-bottom: 16px;
}
#preferences .inputs {
  display: flex;
  flex-direction: row;
  align-items: center;
}
#preferences .input {
  position: relative;
}
#preferences .input.half {
  width: 50%;
}
#preferences .input.quarter {
  width: 25%;
}
#preferences .input.third {
  width: 33.333333%;
}
#preferences .input.two-third {
  width: 66.666666%;
}
#preferences .input.three-quarter {
  width: 75%;
}
#preferences .input.quarter.notlast,
#preferences .input.third.notlast,
#preferences .input.half.notlast {
  margin-right: 3px;
}

#preferences .folder .input {
  margin-top: 3px;
}
#preferences .input .btns {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#preferences .input .btns img {
  width: 18px;
  height: 18px;
  vertical-align: middle;
}
#preferences .input .btns .btn {
  opacity: 1;
  padding: 7px 8px 7px 8px;
  padding: 7px 10px;
}
#preferences .input .btns .btn svg {
  vertical-align: middle;
}
#preferences .input .btns .btn.disabled {
  opacity: 0.3;
}
#preferences .input .btns .btn:not(.disabled):hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.04);
}

#preferences label {
  font-size: 10px;
  margin-right: 8px;
  text-transform: uppercase;
}
#preferences input[type="radio"] {
  display: inline-flex;
  vertical-align: middle;
  margin-top: -2px;
}
#preferences input[type="checkbox"] {
  display: inline-flex;
  vertical-align: middle;
  margin-top: 1px;
}
#preferences input[type="url"],
#preferences input[type="text"] {
  background: none;
  border: none;
  border-radius: 6px;
  background: rgba(0, 0, 0, 0.04);
  color: rgb(0, 98, 163);
  margin: 0;
  width: 100%;
  font-size: 11px;
  padding: 12px 12px 12px 12px;
}

@media (prefers-color-scheme: dark) {
  #preferences {
    color: rgb(255, 255, 255);
    background-color: rgb(40, 40, 40);
  }
  #preferences input[type="url"],
  #preferences input[type="text"] {
    background: rgba(255, 255, 255, 0.04);
    color: rgb(6, 134, 220);
  }
}
