#titlebar {
  opacity: 1;
  -webkit-app-region: drag;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
#titlebar.autohide {
  opacity: 0;
  transition: opacity 0.3s ease;
}
#player.minimized #titlebar,
#player.miniplayer #titlebar {
  -webkit-app-region: no-drag !important;
}
#titlebar.autohide:hover {
  opacity: 1;
}
#titlebar .logo {
  width: 16px;
  height: 16px;
  margin: 4px 10px;
  background-image: url(../../assets/images/logo-512x512.png);
  background-repeat: no-repeat;
  background-size: 100%;
}
#titlebar .appName {
  flex: 1;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;
  font-weight: 500;
  padding-left: 2px;
}

#titlebar .winCtrls-left {
  /* flex-grow: 1; */
  display: flex;
  padding-right: 16px;
  margin-right: 16px;
  border-right: 1px solid rgba(255, 255, 255, 0.4);
}
#titlebar .winCtrls-right {
  -webkit-app-region: no-drag;
  display: flex;
}
#titlebar .winCtrls-left .ctrl {
  -webkit-app-region: no-drag;
}
#titlebar .winCtrls-left .ctrl,
#titlebar .winCtrls-right .ctrl {
  padding: 8px 15px;
  opacity: 0.7;
}
#titlebar .winCtrls-left .ctrl:hover,
#titlebar .winCtrls-right .ctrl:not(.disabled):hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.1);
}
#titlebar .winCtrls-left.player .ctrl:hover,
#titlebar .winCtrls-right.player .ctrl:hover,
#titlebar .winCtrls-right.preview .ctrl:hover {
  background-color: rgba(255, 255, 255, 0.2);
}
#titlebar .winCtrls-right .ctrl:not(.disabled):last-child:hover {
  background-color: rgb(219, 64, 64);
}
#titlebar .winCtrls-left .ctrl svg,
#titlebar .winCtrls-right .ctrl svg {
  color: rgb(0, 0, 0);
  display: block;
}
#titlebar .winCtrls-left.player .ctrl svg,
#titlebar .winCtrls-right.player .ctrl svg,
#titlebar .winCtrls-right.preview .ctrl svg {
  color: rgb(255, 255, 255);
}
#titlebar .winCtrls-right .ctrl:not(.disabled):last-child:hover svg {
  color: rgb(255, 255, 255);
}

#titlebar .winCtrls-right .ctrl.disabled {
  opacity: 0.3;
}
