  opacity: 1;
  background-color: rgb(240, 240, 240);
  box-shadow: 3px 5px 10px 0px rgba(0, 0, 0, 0.3);
} */

@media (prefers-color-scheme: dark) {
  #titlebar .appName {
    color: rgb(200, 200, 200);
  }
  #titlebar .winCtrls-left:not(.disabled) .ctrl svg,
  #titlebar .winCtrls-left:not(.player) .ctrl svg,
  #titlebar .winCtrls-right:not(.player) .ctrl svg,
  #titlebar .winCtrls-right:not(.preview) .ctrl svg {
    color: rgb(255, 255, 255);
  }

  #titlebar .winCtrls-left:not(.disabled) .ctrl:hover,
  #titlebar .winCtrls-left:not(.player) .ctrl:hover,
  #titlebar .winCtrls-right:not(.player) .ctrl:hover,
  #titlebar .winCtrls-right:not(.preview) .ctrl:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
}
