#preview {
  z-index: 1000;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 1px;
  bottom: 1px;
  color: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
  border: 1px solid rgb(60, 60, 60);
  transition: all 0.3s ease;
}
#preview.noborders {
  right: 0px;
  bottom: 0px;
  border: none;
}
#preview .preview-controls {
  opacity: 0;
  transition: opacity 0.3s ease;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  max-height: 64px;
  display: flex;
  justify-content: center;
}
#preview .preview-controls:hover {
  opacity: 1;
}
#preview .preview-controls .preview-item {
  cursor: pointer;
  width: 64px;
  height: 64px;
  background-color: rgb(180, 180, 180);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
#preview .preview-controls .preview-item.active {
  width: 62px;
  height: 62px;
  border: red solid 1px;
}

#preview-video,
#preview-image {
  display: flex;
  position: absolute;
  top: 1px;
  left: 1px;
  right: 1px;
  bottom: 1px;
}
#preview-video video {
  max-width: 100%;
  max-height: 100%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#preview-image img {
  max-width: 100%;
  max-height: 100%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
#preview-video.hidden,
#preview-image.hidden,
#preview-loading.hidden {
  display: none;
}

#preview-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 1px;
  bottom: 1px;
}
#preview-loading div {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
}
