#scanner {
  -webkit-app-region: drag;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  color: rgb(0, 0, 0);
  background-color: rgb(230, 230, 230);
  border: 1px solid rgb(230, 230, 230);
}
#scanner main {
  flex-grow: 1;
  padding: 0px 96px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#scanner header {
  padding: 24px 48px 0px 48px;
}
#scanner header div:nth-child(1) {
  font-size: 28px;
  text-transform: capitalize;
}
#scanner header div:nth-child(2) {
  font-size: 14px;
  padding-left: 1px;
  opacity: 0.8;
}

#scanner .items {
  width: 100%;
}
#scanner .item:not(:last-child) {
  /* margin-bottom: 20px; */
  padding-bottom: 20px;
}
#scanner .item.disabled {
  opacity: 0.5;
}
#scanner .item .name {
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 8px;
}
#scanner .item .labels {
  display: flex;
  padding: 2px 12px;
}
#scanner .item .labels .label-number {
  font-size: 10px;
  min-width: 24px;
}
#scanner .item .labels .label-media {
  flex-grow: 1;
  line-height: 100%;
  font-size: 10px;
  font-weight: 500;
  padding: 2px 12px 0px 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#scanner .item .labels .label-total {
  font-size: 10px;
  min-width: 24px;
  text-align: right;
  transition: transform 0.1s ease;
}
#scanner .item .labels .label-total.finished {
  transform: scale(1.5);
  font-weight: 500;
  color: rgb(40, 145, 243);
}
#scanner .item .progress {
  margin: 4px 6px 0px 6px;
  height: 3px;
  background-color: rgba(0, 0, 0, 0.05);
}
#scanner .item .progress div:nth-child(1) {
  width: 0%;
  height: 3px;
  background-color: rgb(40, 145, 243);
}
#scanner .item .progress div:nth-child(2) {
  width: 0%;
  height: 3px;
  background-color: rgb(231, 196, 39);
}

#scanner footer {
  -webkit-app-region: no-drag;
  text-align: right;
  padding: 16px 48px 24px 48px;
}
#scanner footer button span {
  font-size: 11px;
  text-transform: uppercase;
}

@media (prefers-color-scheme: dark) {
  #scanner {
    color: rgb(255, 255, 255);
    background-color: rgb(40, 40, 40);
    border: 1px solid rgb(40, 40, 40);
  }
  #scanner .item .progress {
    background-color: rgba(255, 255, 255, 0.08);
  }
  #scanner .item .progress div:nth-child(1) {
    background-color: rgb(70, 165, 255);
  }
  #scanner .item .progress div:nth-child(2) {
    background-color: rgb(231, 196, 39);
  }
}
