#controller {
  z-index: 1000;
  position: absolute;
  top: auto;
  left: auto;
  right: 0;
  bottom: 0;
  width: 440px;
  height: 580px;
  color: rgb(255, 255, 255);
  background-color: rgb(0, 0, 0);
  border: 1px solid rgb(60, 60, 60);
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.3);
  overflow: scroll;
}

#controller label {
  font-size: 10px;
  margin-right: 8px;
  text-transform: uppercase;
}
#controller input[type="radio"] {
  display: inline-flex;
  vertical-align: middle;
  margin-top: -2px;
}
