html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

input,
textarea {
  transition: border 0.5s;
}
input:focus {
  outline: none !important;
  border: 1px solid rgb(0, 98, 163);
}
textarea:focus {
  outline: none !important;
  border: 1px solid rgb(0, 98, 163);
}

.nocursor {
  cursor: none;
}

::-webkit-scrollbar {
  display: none;
}

