.button-container button:not(:first-child):not(.link) {
  margin-left: 4px;
}
.button-container button.link:not(:first-child) {
  margin-left: 2px;
}
button {
  background: none;
  border: none;
  color: rgb(0, 0, 0);
  font-weight: 400;
  padding: 6px 16px 7px 16px;
}
button:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.1);
}
button.link {
  padding: 6px 10px;
}
button.gray {
  background-color: rgb(200, 200, 200);
  color: rgb(0, 0, 0);
}
button.accent {
  color: rgb(0, 98, 163);
}
button.danger {
  color: rgb(202, 50, 23);
}
button.link:hover {
  color: rgb(0, 98, 163);
}
button.gray:hover {
  background-color: rgb(190, 190, 190);
}
button.accent:hover {
  color: rgb(0, 98, 163);
}
button.danger:hover {
  color: rgb(248, 58, 25);
}
button svg {
  margin-left: -6px;
  margin-right: 8px;
  vertical-align: middle;
}
button.link svg {
  margin-left: 0px;
}
button span {
  vertical-align: middle;
}
button.rounded {
  border-radius: 4px;
}
button.fullwidth {
  width: 100%;
}

a:link,
a:visited,
a:active {
  font-size: 11px;
  font-weight: 500;
  text-decoration: none;
  color: rgb(0, 98, 163);
  text-transform: lowercase;
}
a:hover {
  color: rgb(0, 112, 187);
}

[contenteditable="true"]:empty:before {
  content: attr(placeholder);
  pointer-events: none;
  color: rgba(0, 0, 0, 0.5);
}
