#playlist {
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  height: calc(100vh - 3px);
  background-color: rgb(230, 230, 230);
  display: flex;
  flex-direction: column;
}

@media (prefers-color-scheme: dark) {
  #playlist {
    color: rgb(255, 255, 255);
    background-color: rgb(40, 40, 40);
  }
}
