#profile .topic {
  font-size: 11px;
  font-weight: 500;
  text-transform: uppercase;
  margin-top: 16px;
  margin-right: 32px;
  margin-bottom: 12px;
}
#profile .topic:first-child {
  margin-top: 32px;
}
#profile .topic:not(:first-child) {
  margin-top: 24px;
}
#profile header.top {
  position: relative;
  padding: 24px 0px 0px 64px;
}
#profile .container {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  overflow-y: hidden;
}
#profile .container .left {
  width: 28%;
  min-width: 360px;
  padding: 28px 16px 12px 64px;
}
#profile .container .right {
  /* flex: 1 1 auto; */
  width: 75%;
  display: flex;
  flex-direction: column;
  padding-top: 32px;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 32px;
  overflow: hidden;
}
#profile .links {
  position: absolute;
  right: 24px;
  bottom: 12px;
}
#profile .links a {
  padding: 4px 8px;
  border-radius: 4px;
  background: rgb(220, 220, 220);
}
#profile .links a:hover {
  background: rgb(210, 210, 210);
}
#profile .links a:not(:first-child) {
  margin-left: 4px;
}

#profile .aka {
  font-size: 13px;
  line-height: 110%;
  min-height: 16px;
}
/* #profile .aka input {
  background: none;
  border: none;
  line-height: 110%;
  width: 100%;
  padding: 0px;
} */
#profile .title {
  font-size: 28px;
  line-height: 100%;
  text-transform: uppercase;
}
#profile .subtitle {
  font-size: 13px;
  line-height: 110%;
  padding-left: 1px;
}
#profile .tags {
  font-size: 9px;
  font-weight: 600;
  padding-bottom: 4px;
  opacity: 0.7;
  text-transform: uppercase;
  text-align: right;
  padding-right: 6px;
  overflow-x: scroll;
  white-space: nowrap;
}
/* #profile .tags input {
  background: none;
  border: none;
  font-size: 9px;
  font-weight: 600;
  text-transform: uppercase;
  width: 100%;
  padding: 0px;
} */
#profile .previews {
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 6px 6px 10px 1px rgba(0, 0, 0, 0.3);
  height: calc(100% - 54px);
}
#profile .previews img {
  cursor: pointer;
  display: block;
  width: 100%;
  height: calc(100% - 64px);
  object-fit: cover;
  transition: transform 0.3s ease;
}
#profile .previews img.expanded {
  height: 100%;
}
#profile .previews img:hover {
  transform: scale(1.32);
}
#profile .previews .previews-items {
  width: 100%;
  height: 64px;
}
#profile .previews .previews-items .preview-item {
  cursor: pointer;
  width: 64px;
  height: 64px;
  display: inline-block;
  background-color: rgb(0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: transform 0.2s ease;
}
#profile .previews .previews-items .preview-item:hover {
  transform: scale(1.3);
}
#profile .description {
  font-family: SourceSansProLight;
  font-weight: 500;
  font-size: 15px;
  line-height: 125%;
  padding: 6px 6px 16px 0px;
  margin-bottom: 6px;
  max-height: 136px;
  overflow-y: scroll;
  transition: max-height 0.2s ease;
}
