}
#profile .description textarea {
  background: none;
  border: none;
  font-family: SourceSansProLight;
  font-weight: 500;
  font-size: 15px;
  line-height: 125%;
  padding: 0px 0px 0px 0px;
  margin-bottom: 16px;
  width: 100%;
  height: 136px;
} */
#profile .description.collapsed {
  max-height: 20px;
  transition: max-height 0.2s ease;
}
#profile .description.collapsed.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#profile .description .collapse {
  opacity: 0.7;
  transform: rotate(0deg);
  float: right;
  padding: 0px 10px;
  transition: transform 0.2s ease;
}
#profile .description .collapse:hover {
  opacity: 1;
  cursor: pointer;
}
#profile .description .collapse.hide {
  transform: rotate(180deg);
  transition: transform 0.2s ease;
}

#profile .description.greater {
  max-height: 344px;
}
#profile .collection-label {
  font-size: 10px;
  font-weight: 500;
  padding: 12px 12px 12px 12px;
  margin-left: -3px;
  margin-top: 12px;
  text-transform: uppercase;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.07);
}

#profile .infos {
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  align-items: center;
}
#profile .infos .info {
  padding-right: 24px;
}
#profile .infos .info div:first-child {
  font-size: 8px;
  text-transform: uppercase;
  opacity: 0.7;
  padding-bottom: 1px;
}
#profile .infos .info div:last-child {
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase;
  margin-bottom: 6px;
}

#profile .collection-container {
  flex: 1 1 1px;
  overflow-y: scroll;
  /* padding-right: 24px; */
  padding-top: 16px;
}
#profile .collection-container.course {
  padding-left: 24px;
}
#profile .collection {
  display: flex;
  flex-direction: row;
  margin-top: 24px;
  margin-bottom: 12px;
}
#profile .collection .collection-date {
  font-size: 10px;
  font-weight: 500;
  width: 72px;
  text-align: right;
  padding-top: 6px;
  padding-bottom: 2px;
  line-height: 100%;
}
#profile .collection .collection-type {
  font-size: 9px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: right;
  width: 72px;
  line-height: 100%;
  background-color: transparent;
}
#profile .collection.author .collection-date {
  width: 50px;
}
#profile .collection.author .collection-type {
  width: 50px;
}
#profile .collection .collection-image {
  width: 64px;
  height: 64px;
  background-color: rgba(0, 0, 0, 0.1);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 4px 4px 4px 1px rgba(0, 0, 0, 0.3);
  transition: transform 0.1s ease;
  margin: 0px 16px;
}
#profile .collection .collection-image.season {
  height: 86px;
  transition: width 0.1s ease;
}
#profile .collection .collection-image:not(.season):hover {
  cursor: pointer;
  transform: scale(1.1);
}
#profile .collection .collection-image.season:hover {
  cursor: pointer;
  width: 144px;
}
#profile .collection .collection-image.book {
  height: 102px;
}
#profile .collection .collection-texts {
  flex: 1;
  align-self: center;
}
#profile .collection .collection-aka {
  font-size: 12px;
  /* padding-top: 4px;
  padding-bottom: 2px; */
}
#profile .collection .collection-title {
  font-size: 15px;
  font-weight: 500;
}
#profile .collection .collection-description {
  font-family: SourceSansProLight;
  font-weight: 500;
  font-size: 13px;
  line-height: 125%;
  padding-top: 4px;
  /* padding: 6px 48px 6px 0px; */
  max-height: 64px;
  overflow-y: scroll;
}

#profile .medias {
  padding: 0px 0px 12px 54px;
}
#profile .medias.author {
  padding: 0px 0px 12px 56px;
}
#profile .medias.nocollection {
  padding: 0px 0px;
}
#profile .medias .media {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 12px;
  max-width: 400px;
  overflow: hidden;
  transition: max-width 0.2s ease;
}
#profile .medias .media.hovered {
  margin-top: -12px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.06);
}
#profile .medias .media:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
}
#profile .medias .media:hover {
  cursor: pointer;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.07);
  max-width: 100%;
}
#profile .medias .media .media-track {
  font-size: 9px;
  font-weight: 500;
  padding-right: 10px;
  min-width: 10px;
  /* display: none; */
}
#profile .medias .media .media-playing {
  position: absolute;
  top: 50%;
  left: 6px;
  bottom: 0;
  height: 16px;
  transform: translateY(-50%);
}
#profile .medias .media .media-texts {
  flex: 1;
  padding: 0px 16px 0px 0px;
  overflow: hidden;
  max-width: 362px;
}
#profile .medias .media .media-texts.playing {
  padding: 0px 16px 0px 5px;
}
#profile .medias .media .media-texts.last-played {
  color: rgb(98, 17, 17);
}
#profile .medias .media .media-texts .media-aka {
  font-size: 10px;
  line-height: 100%;
  padding-bottom: 2px;
}
#profile .medias .media .media-texts .media-title {
  font-size: 13px;
  font-weight: 600;
  line-height: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 362px;
}
#profile .medias .media .media-extra {
  font-size: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
#profile .medias .media .media-extra svg {
  margin: 0px 5px;
}
#profile .medias .media .media-extra .media-duration {
  padding-left: 4px;
  text-transform: uppercase;
  min-width: 38px;
}
#profile .medias .media .media-extra .media-onremote {
  display: none;
}
#profile .medias .media:hover .media-extra .media-onremote {
  display: inherit;
}

#profile .medias .media:hover .media-texts,
#profile .medias .media:hover .media-texts .media-title {
  /* overflow: visible; */
  max-width: none;
}
