@media (prefers-color-scheme: dark) {
  #profile {
    color: rgb(255, 255, 255);
    background-color: rgb(40, 40, 40);
    /* border: 1px solid rgb(40, 40, 40); */
  }
  #profile .description,
  #profile .tags,
  #profile .label,
  #profile table.info tr td:first-child {
    opacity: 0.8;
  }
  #profile .label {
    border-bottom: 1px solid rgba(255, 255, 255, 0.07);
  }

  #profile .medias .media:not(:last-child) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  }
  #profile .medias .media:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.03);
  }
  #profile .collection-label {
    background-color: rgba(255, 255, 255, 0.07);
  }
  #profile .links a {
    background: rgb(60, 60, 60);
  }
  #profile .links a:hover {
    background: rgb(70, 70, 70);
  }
  #profile .medias .media .media-texts.last-played {
    color: rgb(241, 107, 107);
  }
  #profile .medias .media.hovered {
    background-color: rgba(255, 255, 255, 0.06);
  }
}
