#profile-editor {
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  height: calc(100vh - 3px);
  background-color: rgb(230, 230, 230);
  display: flex;
  flex-direction: column;
  padding: 16px 64px 48px 64px;
}
#profile-editor .editor-title {
  font-size: 28px;
  line-height: 110%;
  text-transform: uppercase;
}
#profile-editor .editor-subtitle {
  font-size: 13px;
  line-height: 110%;
  padding-left: 1px;
}
#profile-editor .tab {
  margin-top: 24px;
  display: flex;
  align-items: center;
}
#profile-editor .tab .editor-aka {
  font-size: 13px;
  line-height: 110%;
  min-width: 240px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255, 0.2);
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 20px 16px 8px 16px;
}
#profile-editor .tab .editor-tags {
  flex-grow: 1;
  margin-left: 6px;
  font-size: 13px;
  text-transform: uppercase;
  line-height: 110%;
  border-radius: 5px;
  background-color: rgb(255, 255, 255, 0.2);
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 20px 16px 8px 16px;
}
#profile-editor .editor-images {
  margin-top: 16px;
  height: 96px;
  background-color: rgb(255, 255, 255, 0.2);
  border: 1px solid rgb(0, 0, 0, 0.1);
}
#profile-editor .editor-description {
  flex-grow: 1;
  margin-top: 16px;
  font-family: SourceSansProLight;
  font-weight: 500;
  font-size: 15px;
  line-height: 125%;
  height: 136px;
  overflow-y: scroll;
  border-radius: 5px;
  background-color: rgb(255, 255, 255, 0.2);
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 20px 16px 16px 16px;
}
#profile-editor .editor-infos {
  margin-top: 16px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255, 0.2);
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 20px 16px 16px 16px;
}
#profile-editor .editor-links {
  margin-top: 16px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255, 0.2);
  border: 1px solid rgb(0, 0, 0, 0.1);
  padding: 20px 16px 16px 16px;
}

#profile-editor .label-inside,
#profile-editor [contenteditable="true"] {
  position: relative;
}
#profile-editor .label-inside span,
#profile-editor [contenteditable="true"] span {
  position: absolute;
  top: 2px;
  left: 6px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 9px;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.5);
}
#profile-editor .label-inside:not([contenteditable="true"]) span {
  top: 5px;
}

#profile-editor .buttons {
  margin-top: 24px;
  text-align: right;
}
#profile-editor .buttons.disabled {
  opacity: 0.3;
}

@media (prefers-color-scheme: dark) {
  #profile-editor {
    color: rgb(255, 255, 255);
    background-color: rgb(40, 40, 40);
  }
  #profile-editor .label-inside span,
  #profile-editor [contenteditable="true"] span {
    color: rgba(255, 255, 255, 0.5);
  }
  #profile-editor .tab .editor-aka,
  #profile-editor .tab .editor-tags,
  #profile-editor .editor-images,
  #profile-editor .editor-description,
  #profile-editor .editor-infos,
  #profile-editor .editor-links {
    background-color: rgb(0, 0, 0, 0.2);
    border: 1px solid rgb(255, 255, 255, 0.1);
  }
}
